import { get } from 'lodash';
import { actionChannel } from 'redux-saga/effects';

import { sapApiMap } from '../../utils/apisMap';
import HttpRequestError from '../../utils/HttpRequetsError';
import { sapApi } from '../../utils/request';

export async function getAllReportApi(payload: Partial<any>): Promise<any> {
  const {data} = await sapApi.get(sapApiMap.getting_all_reports+"?pageOffset=1&pageSize=1000");
  if (get(data, 'error') === 200) return data;
  throw new HttpRequestError(data.error, data.messages);
}

export async function getDetailReportApi(payload: Partial<any>): Promise<any> {
  const {data} = await sapApi.get(sapApiMap.getting_detail_reports+"/"+get(payload,"idBaoCao",'')+"?pageOffset=1&pageSize=10000");
  if (get(data, 'error') === 200) return data;
  throw new HttpRequestError(data.error, data.messages);
}

