import produce from 'immer';
import { LOGIN_DONE } from './constants';

export default function(state = {}, action: any): any {
  return produce(
    state,
    (draftState: UserState): any => {
      switch (action.type) {
        case LOGIN_DONE:           
          draftState.loggedInUser = true;
          draftState.accessToken = action.data;
          return draftState;
        default:
          return draftState;
      }
    },
  );
}
