// import { SagaIterator } from 'redux-saga';
import { call, put, takeEvery } from 'redux-saga/effects';

import { GET_ALL_REPORT, GET_DETAIL_REPORT } from './constants';
import { getAllReportDone } from './action';
import { getAllReportApi, getDetailReportApi } from './helpers';

function* takeGetReportSaga(action: any) {
  const res = yield call(getAllReportApi,action.data);
  if(res.data && res.data.length >= 0 )
  action.onSuccess(res.data)
  yield put(getAllReportDone(res.data));
}

function* takeGetDetailReportSaga(action: any) {
    const res = yield call(getDetailReportApi,action.data);
    if(res.data && res.data.length >= 0 )
    action.onSuccess(res.data)
    yield put(getAllReportDone(res.data));
  }

export default function*() {
  yield takeEvery(GET_ALL_REPORT, takeGetReportSaga);
  yield takeEvery(GET_DETAIL_REPORT, takeGetDetailReportSaga);
}
