import { get } from 'lodash';

import { sapApiMap } from '../../utils/apisMap';
import HttpRequestError from '../../utils/HttpRequetsError';
import { sapApi } from '../../utils/request';

export async function loginApi(payload: Partial<any>): Promise<any> {
  const res = await sapApi.post(sapApiMap.login, payload);  
  if (get(res, 'status') === 200) return res.data;
  throw new HttpRequestError(res.status.toString(), [res.statusText]);
}