import { GET_ALL_REPORT_DONE } from "./constants";
import { fromJS } from 'immutable';

export const initialState = fromJS({
    listReport: []
});

const BaoCaoReducer = (state = initialState, action: any) : any => {
    switch (action.type) {
        case GET_ALL_REPORT_DONE:
            return state.set("listReport", action.data)
        default:
            return state
    }
}
export default BaoCaoReducer